  background-color: white;
  border: none;
}
.registration .registration_form footer .sign-btn {
  transition: all 0.2s;
  font-size: 1rem;
  font-weight: 700;
  outline: none;
  color: #F9F9F9;
  background-color: rgb(0, 0, 0);
  padding: 0.3rem;
  padding-inline: 0.4rem;
  border: none;
  border-radius: 0.2rem;
  cursor: pointer;
}
.registration .registration_form footer .sign-btn img {
